<template>
  <div>
    <el-row type="flex" justify="center" class="row" align="middle">
      <el-col :span="5">
        <el-button type="primary" @click="downloadStudent">Student Course</el-button>
      </el-col>
      <el-col :span="5">
        <el-button type="primary" @click="downloadCourse">Course</el-button>
      </el-col>
    </el-row>

    <el-row type="flex" justify="space-around" class="row">
      <el-col :span="11">
        Monday
        <el-table
          border
          @row-click="select"
          :data="courses.mon">
          <el-table-column
            prop="title"
            width="180"
            label="Name">
          </el-table-column>
          <el-table-column
            prop="grade"
            label="Grade">
          </el-table-column>
          <el-table-column
            prop="fee"
            label="Fee">
          </el-table-column>
          <el-table-column
            width="180"
            prop="teacherName"
            label="Teacher">
          </el-table-column>
          <el-table-column
            prop="submitted"
            label="Submitted">
          </el-table-column>
        </el-table>
      </el-col>
      <el-col :span="11">
        Tuesday
        <el-table
          border
          @row-click="select"
          :data="courses.tue">
          <el-table-column
            prop="title"
            width="180"
            label="Name">
          </el-table-column>
          <el-table-column
            prop="grade"
            label="Grade">
          </el-table-column>
          <el-table-column
            prop="fee"
            label="Fee">
          </el-table-column>
          <el-table-column
            width="180"
            prop="teacherName"
            label="Teacher">
          </el-table-column>
          <el-table-column
            prop="submitted"
            label="Submitted">
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>

    <el-row type="flex" justify="space-around" class="row">
      <el-col :span="11">
        Wednesday
        <el-table
          border
          @row-click="select"
          :data="courses.wed">
          <el-table-column
            prop="title"
            width="180"
            label="Name">
          </el-table-column>
          <el-table-column
            prop="grade"
            label="Grade">
          </el-table-column>
          <el-table-column
            prop="fee"
            label="Fee">
          </el-table-column>
          <el-table-column
            width="180"
            prop="teacherName"
            label="Teacher">
          </el-table-column>
          <el-table-column
            prop="submitted"
            label="Submitted">
          </el-table-column>
        </el-table>
      </el-col>
      <el-col :span="11">
        Thursday
        <el-table
          border
          @row-click="select"
          :data="courses.thu">
          <el-table-column
            prop="title"
            width="180"
            label="Name">
          </el-table-column>
          <el-table-column
            prop="grade"
            label="Grade">
          </el-table-column>
          <el-table-column
            prop="fee"
            label="Fee">
          </el-table-column>
          <el-table-column
            width="180"
            prop="teacherName"
            label="Teacher">
          </el-table-column>
          <el-table-column
            prop="submitted"
            label="Submitted">
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import {getAllCourses, downloadStudentCourse, downloadCourse} from 'api/asa'
import {
  mapMutations
} from 'vuex'
import {userValidation} from 'assets/js/mixin'

export default {
  name: 'HelloWorld',
  data () {
    return {
      courses: {}
    }
  },
  created () {
    getAllCourses().then((res) => {
      this.courses = res
    })
  },
  mixins: [userValidation],
  methods: {
    ...mapMutations({
      setCurrentASA: 'SET_CURRENT_ASA'
    }),
    select (obj) {
      this.setCurrentASA(obj)
      this.$router.push('/admin/asa/detail')
    },
    downloadStudent() {
      downloadStudentCourse({
        email: 'jaygao@bcis.net.cn',
        subject: 'Student Course',
        body: 'Student Course',
        filename: 'Student Course.csv'
      }).then((res) => {
        console.log(res)
      })
    },
    downloadCourse() {
      downloadCourse({
        email: 'jaygao@bcis.net.cn',
        subject: 'ASA Course',
        body: 'ASA Course',
        filename: 'ASA Course.csv'
      }).then((res) => {
        console.log(res)
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" scoped>
  @import "~variables"
  .row
    margin-bottom $small-margin
</style>



